"use strict";
const development_core_1 = require('development-core');
const BaseLoader_1 = require('./BaseLoader');
class DirLoader extends BaseLoader_1.BaseLoader {
    constructor(option) {
        super(option);
    }
    load(cfg) {
        let loader = this.option.loader;
        if (loader.dir) {
            return cfg.findTasksInDir(development_core_1.taskSourceVal(loader.dir));
        }
        else {
            return super.load(cfg);
        }
    }
    getTaskDefine() {
        let loader = this.option.loader;
        if (!loader.configModule
            && !loader.module && loader.dir) {
            return development_core_1.findTaskDefineInDir(development_core_1.taskSourceVal(loader.dir));
        }
        else {
            return super.getTaskDefine();
        }
    }
}
exports.DirLoader = DirLoader;
